据类型。在单独一个表达式中可以组合整数、浮点数、甚至是字符。当然，在这种情况下C/C++语言编译器可能需要生成一些指令将某些操作数转换成不同类型，使得硬件可以对表达式进行计算。例如，如果对32位int型数和64位long long型数进行加法操作，那么编译器将安排把32位int型值转换成64位值。如果是int型数据和float型数据进行加法操作，那么编译器将安排把int型值转换成为float格式。这个转换过程稍微复杂一些，因为int型值和float型值的存储方式不同。因为编译器可以自动处理这些转换而无需程序员介入，所以这类转换称为隐式转换(implicit conversion）。 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"ub22be3ab"}},[_c('strong',[_c('span',{staticClass:"ne-text"},[_vm._v(" 当发生下列情况时会进行隐式转换： ")])])]),_c('p',{staticClass:"ne-p",staticStyle:{"margin-left":"2em"},attrs:{"id":"u78063df8"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 1) 当算术表达式或逻辑表达式中操作数的类型不相同时。 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"margin-left":"2em"},attrs:{"id":"u10cac06a"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 2) 当赋值运算符右侧表达式的类型和左侧变量的类型不匹配时。 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u94360c53"}},[_c('strong',[_c('span',{staticClass:"ne-text"},[_vm._v(" 隐式类型转换规则： ")])]),_c('span',{staticClass:"ne-text"},[_c('br')]),_c('span',{staticClass:"ne-text"},[_vm._v(" C/C++自动转换的基本原则是： ")]),_c('strong',[_c('span',{staticClass:"ne-text"},[_vm._v(" 低精度类型向高精度类型转换，有符号类型向无符号类型转换 ")])]),_c('span',{staticClass:"ne-text"},[_vm._v(" 。具体是： ")])]),_c('img',{staticStyle:{"display":"block","width":"100%"},attrs:{"id":"JQc0a","src":require("../assets/images/1671423433758-682eec97-4005-44b5-b214-e4cd5f1f92f8.jpeg")}}),_c('p',{staticClass:"ne-p",staticStyle:{"text-align":"center"},attrs:{"id":"u96aca22b"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 图2.7 数据类型隐式转换 ")])]),_c('p',{staticClass:"ne-p",staticStyle:{"text-indent":"2em"},attrs:{"id":"u4ec1572c"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 由上图可以看到，当数据类型的空间小于int时，都会转换为int，这是“ ")]),_c('a',{staticClass:"ne-link",attrs:{"data-href":"#vLxhS","href":"#vLxhS"}},[_c('span',{staticClass:"ne-text"},[_vm._v(" 整型提升 ")])]),_c('span',{staticClass:"ne-text"},[_vm._v(" ”的原因，详见本章5.3节。另一个需要注意的是虽然long long有64位，float只有32位，但是二者进行运算的时候，会转换为float类型。可以认为整型与浮点型进行运算时，会转